import { zhiCardFactory as cardFactory } from '@ali/4ever-factory';
import locale from '../../locales/zh_CN';
import { VoteCardPlugin } from '@ali/4ever-bamboo';
import createRenderNode from './createRenderNode';


const { createOnAction, createQueries } = VoteCardPlugin;

const createPlugin = (bizConfig) => {
  const voteCardConfig: VoteCardPlugin.VotePluginConfig = {
    ...locale.voteCard,
    ...bizConfig.voteCard,
  };

  const renderNode = createRenderNode(voteCardConfig);
  const onAction = createOnAction(voteCardConfig);
  return {
    cardType: 'vote',
    onAction,
    renderNode,
    queries: createQueries(),
  };
};

const createBiVoteCardPlugin = cardFactory(createPlugin);

export default createBiVoteCardPlugin;
